import { createAction, props } from '@ngrx/store';

import { IDataDictionaryBranch, IPlainObject } from '@helix/platform/shared/api';
import { IViewComponentDesignValidationIssue } from '../public-interfaces/view-component-design-validation-issue.interface';
import {
  IAddNewComponentActionPayload,
  IInitializeComponentModelActionPayload,
  IInitializeDataComponentModelActionPayload,
  ISetChildrenActionPayload,
  ISetComponentDataPayload,
  ISetSettablePropertiesDataDictionaryItem,
  IUpdateComponentModelPayload
} from './view-component.types';

export const componentsRemoved = createAction(
  '[View Component] Components Removed',
  props<{ guids: string[]; selectParent?: boolean }>()
);

export const initializeComponentModels = createAction(
  '[View Component] Initialize Component Models',
  props<{ payload: IInitializeComponentModelActionPayload[] }>()
);

export const initializeDataComponentModels = createAction(
  '[View Component] Initialize Data Component Models',
  props<{ payload: IInitializeDataComponentModelActionPayload[] }>()
);

export const setValidationIssues = createAction(
  '[View Component] Set Validation Issues',
  props<{ guid: string; issues: IViewComponentDesignValidationIssue[] }>()
);

export const setExpressionValidationIssues = createAction(
  '[View Component] Set Expression Validation Issues',
  props<{
    issues: {
      [guid: string]: IViewComponentDesignValidationIssue[];
    };
  }>()
);

export const setComponentData = createAction(
  '[View Component] Set Component Properties',
  props<{ payload: ISetComponentDataPayload[] }>()
);

export const setChildComponents = createAction(
  '[View Component] Set Child Components',
  props<{ payload: ISetChildrenActionPayload }>()
);

export const setBreadcrumbs = createAction(
  '[View Component] Set Breadcrumbs',
  props<{ guid: string; label: string }>()
);

export const selectComponent = createAction('[View Component] Select Component', props<{ guid: string }>());

export const setComponentLayout = createAction(
  '[View Component] Set Component Layout',
  props<{ guid: string; cols: number[] }>()
);

export const moveComponent = createAction(
  '[View Component] Move Component',
  props<{
    guid: any;
    insertIndex: number;
    columnIndex: number;
    outletName: string;
    parentGuid: string;
  }>()
);

export const insertComponent = createAction(
  '[View Component] Insert Component',
  props<{
    data: {
      guid?: string;
      type?: string;
      initialPropertiesByName?: IPlainObject;
    };
    insertIndex: number;
    columnIndex: number;
    outletName: string;
    targetGuid: string;
  }>()
);

export const addNewComponents = createAction(
  '[View Component] Add New Components',
  props<{ payload: IAddNewComponentActionPayload[] }>()
);

export const updateComponentModel = createAction(
  '[View Component] Update Component Model',
  props<{ payload: IUpdateComponentModelPayload[] }>()
);

export const removeComponents = createAction(
  '[View Component] Remove Components',
  props<{ guids: string[]; selectParent?: boolean }>()
);
